.cartWrap {
    margin-top: 50px;
    display: flex;
    justify-content: space-between;
}

.cartLeft{
    width: 60%;
    font-size: 13px;
    margin-right: 100px;
}
.cartLeft>p {
    text-align: left;
}
.cartLeft tr {
    height: 165px;
    margin: 0;
   
    
}
.cartLeft .table td {
    line-height: 60px;
    border-bottom: 1px solid #dee2e6;
}
.tdImg{
    width: 110px;
    height: 150px;
    padding: 0;
    
}
.cartLeft img {
    display: block;
    width: 110px;
    height: 150px;
}



.cartRight {
    width: 40%;
    font-size: 13px;
}
.cartRight .table {
    border-top: 1px solid #dee2e6;
    border-bottom: 1px solid #dee2e6;
}
.cartRight .table td {
    border: none;
  
}
.priceTitle,
.ship {
    text-align: left;
}
.priceCart,
.shipFee{
    text-align: right;
}
.total {
    text-align: right;
}

.minus,
.plus {
    
    cursor: pointer;
    margin: 0 5px;
    
}
.alert {
    width: 100%;
    background: #ffefa1;
    position: relative;
}

.popupClose {
    border: none;
    outline: none;
    background:none;
    color: rgb(70, 67, 67);
    position: absolute;
    top:0;
    right: 0;
}

.closebtn{
     border: none;
     outline: none;
     background: none;
    color: black;
     
}
.cartEmpty {
    font-size: 30px;
    color: lightgray;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

/* 반응형 */

@media screen and (max-width:767px) {
    .cartWrap{
        width: 100%;
        flex-wrap: wrap;
        
    }
    .cartLeft{
        width: 100%;
    }
    .cartLeft .table td{
        line-height: 50px;
    }
    .cartRight{
        width: 100%;
    }
}
