.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

/* font */
 @font-face {
   font-family: "canela";
   src: url(./fonts/Canela-RegularItalic.woff);
 }

 

/* navbar */
li {
  list-style: none;
}

.nav-link {
  font-size: 14px;
}

.gnb img {
  display: block;
  width: 20px;
  height: 20px;

}
/* landing maing page */
.mainPage {
  margin-top: 100px;
}
.mainImg {
  overflow: hidden;
  border-radius: 38% 62% 63% 37%/41% 44% 56% 59%;
 background: whitesmoke;
}
.mainImg>img{
  width: 100%;
  height: 110%;
  object-fit: cover;
}
.mainText {
  margin-top: 100px;
display: flex;
flex-direction: column;
align-items: center;
background-image: url(./bg3.png);
background-size: 100% 100%;
background-position: 90% 100%;
background-repeat: no-repeat;
  
}
.mainText>h1{
  font-size: 60px;
  text-align: left;
  color: #ffaec0;
  font-weight: 600;
  font-family: 'canela';
  margin: 0;

}
.mainText>button{
  margin: 30px 30px 0 0;
  border: none;
  outline: none;
  border-radius: 10px;
  padding: 10px;
  width: 200px;
  background-color: #ffaec0;
  color: whitesmoke;
}




/* women,men,all main page */
.main {
  width: 1400px;
  height: auto;
margin: auto;
}
.main>div{
  width: 100%;
  min-width: 1200px;
   display: flex;
   flex-wrap: wrap;
   
  
}
.womenCard{
  width: 32%;
  height: 600px;
  margin: 0.5%;
  cursor: pointer;
  
}
.womenCard>img {
  display: block;
  width: 100%;
  height: 85%;
  margin: 10px;
}
.textBox{
  height: 15%;
  text-align: left;
  padding: 10px;
  margin-left: 5px;
  box-sizing: border-box;
}
.textBox>h4 {
  font-size: 14px;
}
.textBox>p {
  font-size: 12px;
}


/* mobile */

@media screen and (max-width:767px) {
  .main {
    width: 100%;
  }
  .main>div {
    min-width: 100%;
  }
  .womenCard{
    width: 100%;
    height: 80vh;
  }
}




@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
