

.description {
    text-align: left;
    padding-left: 50px;
    color: #444444;
}
.title {
    font-size: 15px;
    
}
.subtitle {
    font-size: 13px;
}
.price {
    font-size: 13px;
}

.sizeWrap {
    padding: 0;
    margin: 0;
    display: flex;
}
.sizeWrap>li {
    margin-right: 5px;
    width: 30px;
    height: 30px;
    line-height: 30px;
    text-align: center;
    border-radius: 5px;
    border: 1px solid lightgray;

}
.sizeWrap>li>a {
    display: block;
    font-size: 10px;
    color: rgb(51, 50, 50);
}

.detailBtn {
    width: 100%;
    height: 30px;
    border: none;
    outline: none;
    background: #ffefa1;
    
}

.tabmenu {
    display: flex;
    padding: 0;
    margin: 0;
}
.tabmenu>li{
    margin-right: 5px;
}
.tabmenu>li>a{
    color: gray;
    font-size: 13px;
    
}
.content {
    font-size: 13px;

}

